import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/home'
    },{
      path:'/home',
      name:'home',
      component:require('../components/Home.vue')
    },
      {
      path: '/reader/:id',
      name: 'reader',
      component: require('../components/Reader.vue')
    },
    {
      path: '/bookdetail/:id',
      name: 'bookdetail',
      component: require('../components/BookDetail.vue')
    },
    {
      path: '/category',
      name: 'category',
      component:require('../components/Category.vue')
    },
  ]
})



